




































































































































































































.template {
  background-color: #fff;
  color: rgb(17, 17, 17);
  width: 100%;
  min-height: 29.6cm;
  word-break: break-all;
  position: relative;
  overflow: hidden;
}
  .padding {
    padding: 40px;
  }

.logo {
  width: 70%;
}

.header {
  text-align: left;
  border-bottom: 1px solid rgb(177, 177, 177);
  h1 {
    text-transform: uppercase;
    font-size: 38px;
    font-weight: bold;
  }
  p {
    font-weight: 500;
    color: rgb(143, 143, 143);
    font-size: 0.975rem;
    margin-bottom: 1rem;
  }
}

.col-left {
  padding-top: 20px;
  padding-left: 30px;
  flex-shrink: 0;
  width: 200px;
  height: 100%;
  border-left: 1px solid rgb(177, 177, 177);
}

.col-right {
  flex-grow: 1;
  padding: 30px;
  padding-left: 0;
}

.title {
  position: relative;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 20px;
  letter-spacing: 1px;
  &::before {
    content: '';
    display: block;
    position: absolute;
    width: 30px;
    height: 2px;
    background-color: #000;
    left: 0;
    bottom: -3px;
  }
}

.subtitle {
  font-size: 13px;
  font-weight: bold;
  text-transform: uppercase;
}

.description {
  font-size: 11px;
  font-weight: 500;
  color: rgb(114, 114, 114);
  margin-bottom: 0;
}

.date {
  font-size: 10px;
  font-weight: 500;
  margin-bottom: 0.3rem;
}

.skill-name {
  font-size: 13px;
  color: #000;
  margin-bottom: 0.1rem;
}

.progress {
  border-radius: 0;
  height: 4px;
  background-color: rgb(181, 181, 181);
  .progress-bar {
    background-color: #000;
  }
}

.languages {
  p {
    font-size: 12px;
    margin-bottom: 0;
    font-weight: 500;
  }
}
